.header {
  background-color: #252525;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  color: #FFF;
  font-family: 'Poppins';
  font-size: 32px;
  font-weight: bold;
}

.search-form {
  width: 800px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: stretch;
}

.input {
  /* width: 100%; */
  height: 50px;
  border: 0px;
  background: #EEE;
  border-radius: 50px;
  font-family: 'Open Sans';
  line-height: normal;
  font-size: 14px;
  font-weight: bold;
  color: #050417;
  box-sizing: border-box;
}

.input::-webkit-input-placeholder {
    color: #D5D7E5;
    padding-left: 32px;
}

.input::-ms-input-placeholder {
    color: #D5D7E5;
    padding-left: 32px;
}

.input::placeholder {
    color: #D5D7E5;
    padding-left: 32px;
}

.input-query {
  margin-top: 51px;
  padding-left: 32px;
}

.search-button {
  align-self: center;
  width: 200px;
  margin-top: 75px;
  border: 0px;
  background: #FFF;
  border-radius: 8px;
  font-family: 'Open Sans';
  line-height: normal;
  font-size: 14px;
  letter-spacing: 0.1em;
  color: #252525;
  cursor: pointer;
  text-transform: uppercase;
}

.inline-header {
    position: fixed;
    left: 0px;
    top: 0px;
    min-height: 80px;
    min-width: 100%;
    flex-direction: row;
    box-sizing: border-box;
    overflow: unset;
}

.inline-search-form {
    width: 100%;
    flex-flow: row;
    /* justify-content: center; */
    align-items: center;
}

.inline-input-query {
    width: 800px;
    margin-top: inherit;
    margin-left: 86px;
}

.inline-search-button {
    margin-top: inherit;
    margin-left: 52px;
    margin-right: 106px;
}


@media screen and (max-width: 960px){
   .inline-input-query {
      width: 200px;
      margin-top: inherit;
      margin-left: 30px;
    }
    .input {
      padding: 4px;
    }
    .header {
      font-size: 24px;
    }
    .inline-search-button {
      margin-top: inherit;
      margin-left: 52px;
      margin-right: 56px;
    }

    .search-button {
      align-self: center;
      width: 100px;
    }
}
.popup {
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    overflow-x:hidden;
    overflow-y:auto;
    -webkit-overflow-scrolling:touch;
    z-index:999;
    width:0;
    height:0;
    opacity:0;
}
.popup:target {
	width:auto;
	height:auto;
	opacity:1;
	-webkit-transition:  opacity 1s ease;
	transition: opacity 1s ease;
}
.popup:target .popup-overlay {
	position:fixed;
	top:0;
	bottom:0;
	left:0;
	right:0;
	cursor:pointer;
	background-color:#000;
	background-color:rgba(0, 0, 0, 0.7);
	z-index:1;
}
.popup .popup-dialog {
	border-radius:6px;
	box-shadow:0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
	position:relative;
	/* width: 90%; */
	max-width:660px;
	max-height:70%;
	margin: 50px auto;
	overflow-x:hidden;
	overflow-y:auto;
	z-index:2;
}
.popup-dialog-lg {max-width:820px !important;}

.popup .popup-content {background:#fff; padding:23px 27px;}
.popup .popup-close {
    position:absolute;
	top:13px;
	right:13px;
	color:#0085a6;
	background-color:#fff;
	border-radius:50%;
	height:40px;
	width:40px;
	font-size:30px;
	line-height:37px;
	text-align:center;
	-webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.popup .popup-close:hover {background-color:#0085a6; color:#fff; cursor:pointer;}



.image-content {
    max-width: 940px;
    max-height: 100%;
}

.image {
    width: 100%;
    height: 100%;
}

.user-and-download {
    width: 100%;
    height: 140px;
}

.user-info {
    float: left;
    margin-top: 30px;
    height: 70px;
    width: 280px;
}

.user-image-wrapper {
    float: left;
    max-width: 60px;
    max-height: 60px;
}

.user-image {
    border-radius: 50%;
    width: 100%;
    height: 100%;
}

.user-name-wrapper {
    padding-top: 10px;
    margin-left: 80px;
    background-color: #fff;
    width: 300px;
    height: 60px;
    text-align: start;
}

.user-name-wrapper p {
    margin: 0;
    padding: 0;
}

.user-name-wrapper a {
    text-decoration: none;
}

.user-name {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 1em;
    padding: 0px;
}

.user-username {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    color: #2A2B8D;
    font-size: 0.8em;
    padding: 0px;
    cursor: pointer;
}


.download-button {
    float: right;
    width: 200px;
    height: 60px;
    margin-top: 30px;
    padding: 14px;
    background: #FFFFFF;
    border: 2px solid #2A2B8D;
    box-sizing: border-box;
    border-radius: 10px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 18px;
    color: #2A2B8D;
}

.download-button a::before {
    margin-right: 30px;
    content: url(data:image/png;base64,);
}


.download-button a {
    text-decoration: none;
    color: #2A2B8D;
}

.gmap {
    width: 100%;
    height: 300px;
}
.pagination-wrapper {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    text-align: center;
}

.button-prev {
    cursor: pointer;
    margin-right: 40px;
    height: 100%;
    font-size: 1.4em;
    font-weight: bold;
    padding: 8px;
    background-color: white;
    color: #2A2B8D;
    border: 3px solid #2A2B8D; 
    border-radius: 20%;
}

.button-next {
    cursor: pointer;
    margin-left: 40px;
    height: 100%;
    font-size: 1.4em;
    font-weight: bold;
    padding: 8px;
    background-color: white;
    border: 3px solid #2A2B8D;
    color: #2A2B8D; 
    border-radius: 20%;
}
  
.button-next:hover, .button-prev:hover {
    color: #7FD6F2;
    border-color: #7FD6F2;
}   

.button-next:hover:disabled, .button-prev:hover:disabled {
    cursor: auto;
    color: #2A2B8D; 
    border-color: #2A2B8D; 
}
